$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'colors-and-type';

.#{$className} {
	padding: 0 toRem(20) 0;
	margin: toRem(67) 0 0 toRem(20);
	&__main {
		display: flex;
	}

	&__color-palette {
		margin-top: toRem(1);
		list-style-type: none;
	}

	&__color-palette-item {
		margin-bottom: toRem(20);

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__text-examples {
		list-style-type: none;
		margin-left: auto;
		margin-right: toRem(28);
	}

	&__text-examples-item {
		display: flex;
		margin-bottom: toRem(51);

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__text-examples-title {
		color: $dark_shade_25;
		text-align: right;
		font-weight: 400;
		min-width: toRem(50);	

		&_header {
			text-transform: uppercase;
		}
	}

	&__text-examples-description {
		margin-left: toRem(30);
		white-space: pre;
	}

}